body {
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 20px;
}
.container h2{
    text-align: center;
}

.image-slider {
    display: flex;
    align-items: center;
}

.image-slider .prev, .image-slider .next {
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
    user-select: none;
}

.image-slider .image {
    margin: 0 10px;
    text-align: center;
}

.image-slider .image img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
}

.image-slider .image p {
    margin-top: 10px;
    font-size: 14px;
    color: #333;
}


.category{
    text-align: center;
    
}
.category-container {
    border: 1px solid black;
    border-radius: 30px;
    padding: 5px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.category-button {
    padding: 10px 20px;
    width: 200px;
    margin: 0 5px;
    border: none;
    background-color: #fff;
    color: #333;
    text-decoration: none;
    border-radius: 20px;
}

.category-button:hover {
    background-color: rgb(222, 216, 216);
}